.bannerVideoCoverage {
    position: fixed;
    bottom: 20px;
    left: 20px;
    width: 403px;
    height: 228px;
    margin: 0px;
    padding: 0px;
    border: none;
    border: 1.5px solid white;
    /* box-shadow: -3px 1px 21px -6px rgba(255, 255, 255, 0.75);
-webkit-box-shadow: -3px 1px 21px -6px rgba(255, 255, 255, 0.75);
-moz-box-shadow: -3px 1px 21px -6px rgba(255, 255, 255, 0.75); */

    .bottomVideo {
        background-color: black;
        width: 400px;
        height: 225px;
        background-image: url("../../assets/gif/tv.gif");
    }

    #closeVideo:hover {
        animation: shake;
        animation-duration: 2s;
    }

    video[poster] {
        border: inherit;
        object-fit: cover;
        width: 400px;
        height: calc(300px * 1.71428);
    }

    .vjs-poster {
        background-size: cover;
        background-position: inherit;
    }
}