@import "../colors/index.scss";
@import "../mixins/fonts.scss";
@import "../constants/index.scss";

.mainNavbar {
    position: fixed;
    background-color: $secondColor !important;
    color: $whiteColor !important;
    width: 100%;
    margin: auto !important;
    height: 80px !important;
    z-index: 998;
    font-family: "Cairo", sans-serif;
    padding: 5px 0px 5px 0px;
    box-shadow: -3px 1px 21px -6px rgba(255, 255, 255, 0.75);
    -webkit-box-shadow: -3px 1px 21px -6px rgba(255, 255, 255, 0.75);
    -moz-box-shadow: -3px 1px 21px -6px rgba(255, 255, 255, 0.75);

    * {
        font-family: inherit !important;
        color: inherit !important;
    }

    .animation-overlay {
        position: absolute;
        top: 0px;
        width: 100vw;
        height: 80px;
        background: black;
        /* For browsers that do not support gradients */
        z-index: 9;
    }

    .navbar-dark.bg-dark {
        background-color: $secondColor !important;
    }

    nav {
        @include fontBold();
        background-color: $secondColor;
        height: 80px !important;
        z-index: 999;

        .container {
            .navbar-brand {
                border-color: black;
                color: white;
                font-weight: bold;
                letter-spacing: -0.6px !important;
                padding: 0px 3px 0px 3px;
                font-size: 28px !important;
                text-decoration: wavy;

                @media (max-width: $xx_small) {
                    padding: 0px;
                }
            }

            .navbar-toggler {
                @media (max-width: $xx_small) {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    max-width: 40px;
                    height: 40px;
                }

                .navbar-toggler-icon {
                    @media (max-width: $xx_small) {
                        width: 2rem !important;
                        height: 2rem !important;
                        font-size: 2rem !important;
                    }
                }
            }

            .navbar-collapse {
                position: unset !important;
                background-color: $secondColor !important;
                color: $whiteColor !important;
                left: 0px;
                padding: 10px;
                width: 100vw !important;
                max-width: 100vw !important;

                @media (max-width: $xx_small) {
                    width: 100vw !important;
                    max-width: 100vw !important;
                    position: fixed !important;
                    top: 80px !important;
                }

                @media (min-width: $xx_small) and (max-width: $medium) {
                    position: fixed !important;
                    top: 80px !important;
                }

                .navbar-nav {
                    top: 20px;
                    background-color: $secondColor;
                    padding: 0px 10px 0px 10px;
                }

                ul {
                    li {
                        margin: 0px 5px 0px 5px;

                        a {
                            font-size: 17px;
                            text-transform: none;
                            font-weight: bold;
                            letter-spacing: 0.2px !important;
                            overflow: hidden !important;
                            text-overflow: ellipsis;
                            white-space: nowrap;

                            &:after {
                                position: relative;
                                top: 2px;
                            }
                        }

                        @media (max-width: $medium) {
                            margin: 4px 2.5px 4px 2.5px;
                        }

                        ul.social-urls {
                            background-color: $secondColor;
                            color: $whiteColor;

                            @media (min-width: $large) {
                                margin-left: -35px;
                                width: 220px !important;
                            }

                            li {
                                letter-spacing: -0.25px;
                                font-weight: normal;
                                height: 42px;

                                a {
                                    color: white !important;
                                    text-align: left;
                                    margin: auto;
                                    height: 42px;
                                    letter-spacing: -0.15px;
                                    font-size: 18px;
                                    display: flex;
                                    align-items: center;

                                    img {
                                        margin-right: 10px;
                                    }

                                    &::shadow {
                                        background-color: $primaryColor !important;
                                    }

                                    &.hover {
                                        background-color: black !important;
                                        opacity: 0.8;
                                    }

                                    &.visited {
                                        background-color: $primaryColor !important;
                                    }

                                    &.focus {
                                        background-color: $primaryColor !important;
                                    }

                                    &:active {
                                        background-color: $primaryColor !important;
                                    }

                                    &.link {
                                        background-color: $primaryColor !important;
                                    }
                                }
                            }

                            .divider {
                                height: 5px !important;
                            }
                        }
                    }
                }

                @media (max-width: $large) {
                    width: 100%;
                    position: fixed;
                    top: 80px;
                }

                @media screen and (min-width: $large) {
                    top: 0px;
                    position: relative;
                }
            }

            form {
                input {
                    @media screen and (min-width: $large) {
                        max-width: 220px;
                    }
                }

                button {
                    background-color: $primaryColor;
                    border-color: $primaryColor;
                    border: 2px solid $primaryColor;
                    min-width: 120px;
                    height: 40px;
                    font-family: $defaultFont !important;
                    font-size: 18px;
                    font-weight: 600 !important;
                    outline: none;
                }
            }
        }
    }
}

.top-small-banner {
    cursor: pointer;
    position: absolute;
    top: 85px;
    -webkit-box-shadow: 0px 0px 86px -33px rgba(0, 0, 0, 0.67);
    -moz-box-shadow: 0px 0px 86px -33px rgba(0, 0, 0, 0.67);
    box-shadow: 0px 0px 86px -33px rgba(0, 0, 0, 0.67);
    color: white;
    font-weight: 400;
    background-color: #a435f0;
    width: 100%;
    height: 45px;
    z-index: 9;
    font-family: "Cairo", sans-serif;
    font-weight: 600;
    letter-spacing: -0.3px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;

    a {
        user-select: none;
        color: white;
        letter-spacing: 0px;
        padding: 0px;
        width: 100vw;
        text-align: center;
        font-family: "Lato", sans-serif;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        padding: 0px 10px 0px 10px;
        text-decoration: none;
        font-size: 17px;
        display: flex;
        justify-content: center;
        align-items: center;

        span.iconContainer {
            background-color: white;
            border-radius: 25px;
            width: 115px !important;
            min-width: 100px !important;
            height: 31px !important;
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: inherit;
            margin-right: 6px;
        }

        span.hiddenTextContainer {
            display: none !important;
        }

        span.textContainer {
            margin: -1.3px 0px 0px 0px !important;
            overflow: hidden !important;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-size: 15.5px;
            letter-spacing: 0.4px;
            font-weight: 600;

            @media (max-width: $x_small) {
                padding-right: 45px;
            }
        }
    }
}

.closeButton {
    span {
        color: white !important;
    }
}

.contactCanvas {
    .offcanvas-header {
        display: flex;
        border-bottom: 0.5px solid lightgray;
        align-items: center;

        #offcanvasRightLabel {
            margin-top: 7px !important;
        }
    }

    .contact-form-group {
        margin-left: 0px;
        margin-bottom: 20px !important;

        input {
            width: 100%;
            outline-color: $primaryColor !important;
            box-shadow: $primaryColor !important;
            font-family: $defaultFont;
            color: black;
            font-weight: 500;

            &::placeholder {
                color: grey !important;
            }
        }

        input textarea {
            outline-color: $primaryColor !important;
            box-shadow: $primaryColor !important;

        }

        label {
            color: black;
            font-weight: bold;
            margin-bottom: 7.5px;
        }

        .helper-text {
            margin-left: 2.5px;
            margin-top: 5px;
        }

        .btn-send-contact {
            margin-top: 10px;
            background-color: $primaryColor;
            border-color: $primaryColor;
            width: 100%;
            height: 45px;
            color: white;
            font-family: $defaultFont;
            font-weight: bold;
            letter-spacing: 1px;
        }
    }
}