@import "../../../constants/index.scss";
@import "../../../colors/index.scss";

.initial-section {
    background-color: $primaryColor !important;
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0)), url("../../../../assets/jpg/back.jpg"); //url("../../../../assets/webp/initialBackgroundImage.webp");
    min-height: 900px;
    object-fit: cover;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-position: center !important;
    background-repeat: no-repeat;
    background-size: cover;
    align-items: center;
    display: flex;
    max-width: 100vw;
    overflow-x: hidden !important;

    @media (max-width: $xx_small) {
        height: 250px !important;
    }

    @media (min-width: $xx_small) and (max-width: $x_small) {
        height: 800px !important;
    }

    .text-center {
        margin: auto;
        margin-top: 70px;
        max-width: 40% !important;
        justify-content: center;
        align-items: center;

        .initial-header {
            .header-title {
                font-size: 60px;
                font-family: "Cairo", sans-serif;
                font-weight: bold !important;
                text-overflow: ellipsis;

                @media (max-width: $xx_small) {
                    font-size: 45px !important;
                }

                @media (min-width: $xx_small) and (max-width: $x_small) {
                    font-size: 55px !important;
                }
            }
        }

        .lead {
            color: white;
            line-height: 45px;

            strong {
                color: white;
                font-size: 25px;
                letter-spacing: 0px;
                line-height: 18px !important;
                font-family: "Cairo", sans-serif;

                @media (max-width: $xx_small) {
                    line-height: 24px !important;
                    font-size: 18px !important;
                    color: white;
                }
            }

            @media (max-width: $xx_small) {
                line-height: 20px !important;
                font-size: 13px !important;
                color: white;
            }
        }

        @media (max-width: $medium) {
            max-width: 95% !important;
            margin-top: 80px;
        }

        @media (min-width: $medium) and (max-width: $x_large) {
            max-width: 65% !important;
        }

        &:before {
            left: 50%;
            -webkit-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
            transform: translateX(-50%);
        }

        .initial-header {
            color: white;
            line-height: 55px;
            margin-top: 30px;

            .header-title {
                white-space: normal;
                overflow: hidden;
                text-overflow: hidden;
            }
        }

        a.reach {
            font-size: 24px;
            border-radius: 30px !important;
            font-family: $defaultFont !important;
            letter-spacing: -0.75px;
            font-weight: bolder !important;
            color: black !important;
            background-color: white;
            padding: 5px 25px 5px 25px !important;
            text-decoration: none;
            cursor: pointer;
        }
    }
}

.about-section {
    min-height: 100vh;
    display: flex;
    align-items: center;
    padding-top: 110px;
    padding-bottom: 70px;

    .section-heading {
        font-weight: 700;
        margin: 0px 0px 25px 0px;

        @media (max-width: $medium) {
            margin: -20px 0px 10px 10px;
        }
    }

    a {
        color: black;
        text-decoration: none;
    }

    p {
        line-height: 35px;
        font-size: 22px;
    }

    .image {
        padding: 0px 90px 0px -10px;
        max-height: 750px;
        height: 100%;
        display: flex;

        img {
            width: 95%;
            margin: 0px 30px 0px 0px;
            background-color: #429089;
            object-fit: cover;
            object-position: center center;
        }

        @media (max-width: $small) {
            padding: 0px 0px 0px 20px;
            height: 600px;
            max-width: 100%;

            img {
                margin: 0px 20px 0px 0px;
            }
        }

        @media (min-width: $small) and (max-width: $large) {
            padding: -40px 0px 0px 10px;
            height: 800px;
            max-width: 600px;
        }
    }

    .about {
        max-width: 700px;

        @media (max-width: $medium) {
            max-width: 98%;
            margin-right: 0px;
        }
    }

    .about-text {
        font-size: 24px;
        line-height: 38px;

        @media (max-width: $small) {
            font-size: 21px;
            margin-top: 0px;
            line-height: 34px;
        }
    }
}

.courses {
    width: 100vw;
    padding-bottom: 100px;
    height: auto;
    min-height: 80vh;
    padding-top: 100px;
    background-color: #f8fafb;
    display: block;

    .courses-header {
        margin-bottom: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        letter-spacing: 0.5px !important;
        font-size: 30px;
    }

    .top-container {
        margin-top: 20px;
        min-height: 100%;
        display: flex !important;
        justify-content: center;
        align-items: center;

        .courses-container {
            width: 100%;
            position: relative;

            &::-webkit-scrollbar {
                display: none;
            }

            .courses-card {
                padding: 0px !important;
                display: inline-block !important;
                width: 100%;
                margin: 20px 0px;
                max-width: 350px;
                justify-content: center;
                display: flex;
                box-shadow: 0px 1px 9px -4px rgba(0, 0, 0, 0.51);
                -webkit-box-shadow: 0px 1px 9px -4px rgba(0, 0, 0, 0.51);
                -moz-box-shadow: 0px 1px 9px -4px rgba(0, 0, 0, 0.51);
                white-space: normal;

                .courses-body {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    padding: 15px !important;

                    h1 {
                        font-family: $defaultFont;
                        font-weight: bold;
                        font-size: 21px;
                        padding-right: 20px;
                        display: -webkit-box;
                        height: 54px;
                        letter-spacing: -0.25px;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        line-height: 27px;
                    }

                    .couse-author {
                        color: #6a6f73;
                        line-height: 13px;
                        font-size: 12px;
                        letter-spacing: -0.12px;
                        cursor: pointer !important;
                    }

                    p {
                        margin: 5px 0px 5px 0px;
                    }

                    .couse-information {
                        color: #6a6f73;
                        font-size: 12px;
                        letter-spacing: -0.12px;
                    }

                    .course-price {
                        color: black !important;
                        width: 70px;
                        font-weight: bolder;
                        font-size: 20px;
                        margin-top: 5px;
                        margin-bottom: -5px;
                        letter-spacing: 0px;
                        display: inline-block;
                    }

                    .course-registered {
                        text-align: end;
                        width: calc(100% - 70px);
                        display: inline-block;
                        opacity: 0.9;
                        letter-spacing: -0.25px !important;
                        font-weight: bold;
                        color: black !important;
                        font-size: 18px;
                        margin-top: 5px;
                        margin-bottom: -5px;
                    }

                    .course-stars {
                        display: inline;
                        font-weight: bold;
                        color: #e7a22d !important;
                        display: flex;
                        align-items: center;

                        span {
                            margin-left: 2px;
                        }
                    }

                    .course-star {
                        font-size: 15px;
                        color: #e7a22d !important;
                    }
                }
            }
        }
    }
}

.blog {
    width: 100vw;
    height: auto;
    padding-top: 100px;
    margin-bottom: 100px;
    background-color: #ffffff;

    .blog-header {
        margin-bottom: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        letter-spacing: 0px !important;
        font-size: 33px;

        img {
            margin-top: -6px !important;
        }
    }

    .top-container {
        margin-top: 20px;
        height: 100%;
        display: flex !important;
        justify-content: center;
        align-items: center;

        .blog-container {
            width: 100%;
            position: relative;

            &::-webkit-scrollbar {
                display: none;
            }

            .blog-card {
                width: 18rem;
                padding: 0px !important;
                display: inline-block !important;
                margin: 20px 0px 20px 0px;
                width: 100%;
                justify-content: center;
                display: flex;
                box-shadow: 0px 1px 9px -4px rgba(0, 0, 0, 0.51);
                -webkit-box-shadow: 0px 1px 9px -4px rgba(0, 0, 0, 0.51);
                -moz-box-shadow: 0px 1px 9px -4px rgba(0, 0, 0, 0.51);
                white-space: normal;

                img {
                    cursor: pointer;
                }

                .blog-body {
                    max-height: 450px;
                    overflow: hidden;
                    text-overflow: ellipsis;

                    .blog-information {
                        font-family: $defaultFont;
                        color: #8a8a8a;
                        font-weight: 400;
                        letter-spacing: 0.25px;
                        font-size: 16.5px;
                        height: 19px;
                        width: 100%;

                        .blog-author {
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            padding-right: 3px;
                        }

                        .blog-pub-date {
                            padding-left: 3px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                    }

                    .blog-title {
                        font-family: $defaultFont;
                        font-weight: bold;
                        letter-spacing: -0.25px;
                        display: -webkit-box;
                        height: 62px;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        line-height: 31px;
                    }

                    .blog-content {
                        font-family: $defaultFont;
                        font-size: 18px;
                        color: #555555;
                        font-weight: normal;
                        letter-spacing: -0.25px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        -webkit-line-clamp: 3;
                        -webkit-box-orient: vertical;
                        display: -webkit-box;
                        line-height: 1.5;
                        max-height: 180px;
                    }

                    hr {
                        margin: 0px -10px 0px -10px !important;
                    }

                    .readBtn {
                        width: 100%;
                        height: 50px;
                        position: relative;
                        margin: 5px 0px 5px 0px;
                        top: 5px;
                        font-size: 18px;
                        letter-spacing: 1px;
                        text-decoration: none;
                        font-weight: bold;
                        background-color: $primaryColor;
                        color: white;
                        outline: none;
                        border: none !important;
                        border-radius: 3px;
                    }
                }
            }
        }
    }
}

.in-between {
    width: 100vw;
    background: #9146ff; //#F8DC3D;
    background-position: bottom center;
    min-height: 80vh;
    font-family: tahoma;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0px;
    overflow: hidden;
    position: relative;
    z-index: 1;

    .twitch-logo {
        position: absolute;
        left: 50%;
        top: 40px;
        width: auto;
        height: 50px;
        transform: translateX(-50%);

        @media (max-width: $medium) {
            top: 30px;
        }
    }

    @media (max-width: $medium) {
        height: 100vh;
    }

    &:before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.1);
        z-index: -1;
    }

    h4 {
        font-family: $defaultFont;
        font-size: 40px;
        font-weight: bolder;
    }

    .padder {
        width: 100%;
        min-height: 55vh;
        margin-top: 30px;
        border: 5px solid white;
        box-shadow: 0px 2px 78px -11px rgba(0, 0, 0, 0.75);
        -webkit-box-shadow: 0px 2px 78px -11px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 0px 2px 78px -11px rgba(0, 0, 0, 0.75);

        @media (max-width: $medium) {
            min-height: 75vh;
        }

        iframe {
            flex: 1;
            min-height: 100%;
            height: 100%;
        }
    }
}