@import "../constants/index.scss";

footer {
    background-color: black;
    color: white;
    min-height: 40vh;

    .footerInfo {
        margin: auto;
        padding: 40px 20px 0px 0px;

        .footerInfoRow {
            display: flex;

            img {
                border-radius: 50px;
            }

            .information {
                display: flex;
                justify-content: center;
                flex-direction: column;
                padding: 0px 0px 0px 15px;

                b {
                    font-size: 19px;
                }
            }

        }

        .intro-text {
            padding-top: 15px;
            font-family: $defaultFont !important;
            font-size: 18.1px !important;
            line-height: 28px;

            a {
                color: white;
            }
        }

    }

    .spotify {
        padding: 40px 0px 0px 0px;

        h5 {
            margin: 0px 0px 20px 0px;
            text-align: left;
            font-family: $defaultFont;
            font-weight: bold;
            font-size: 17px;
        }
    }

    .copyright {
        margin-top: -15px;
        text-align: center;
        font-weight: bold;
        font-size: 18px;
        letter-spacing: 0.1px;
    }
}